import './App.css';
import ThemeSelector from './ThemeSelector';
import { useState } from 'react';
import { CodeBlock, obsidian } from "react-code-blocks";
import raw from 'raw.macro';
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'

function App() {
  const [visible, setVisible] = useState({1:false, 2:false, 3:false, 4:false, 5:false, 6:false});
  const A1code = raw('./A1code.txt');
  const A2code = raw('./A2code.txt');

  return (
    <div className="App">

        <div className="theme-selector">
          <ThemeSelector/>
          <h1>Welcome to my 439 class page!</h1>
          <img className="picture" src="./me.jpg"/>
          <h3 className="body-text">
              Hello! My name is Edgar! I'm a senior in HCDE taking the data science pathway. I have a cat, she is very soft and fluffy.
              And I like food. Cinnamon rolls especially are yummy.
          </h3>
          <div className="content">

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 1: !visible[1]}))}}>
              <h3 className="menu-text">Assignment 1: Blink!</h3>
            </div>
            {visible && visible[1] && 
              <div style={{textAlign: "left"}}>
                <h2>REASONING</h2>
                <h3>
                  I used green, red, and "yellow" (they look orange to me) LEDs, each one having a voltage drop of 1.8V. 
                  The Arduino Uno gets 5V from the USB port. So subtracting the voltage drop of a single LED from the 
                  volts coming from the source, 5V - 1.8V, we're left with 3.2 volts. Each led has a current of 20 mA (or 0.02 A). 
                  So to find the resistance needed, I divided the volts by the current (3.2 volts / 0.02 A) and got 160 ohms. 

                  But wait, there's more. Since I'm doing a charlieplex system, each circuit encounters two resistors. So I divided
                  the 160 ohms in half to get 80 ohms. Ideally each pin needs 80 ohms of resistance. I don't have 80 ohm resistors,
                  so I used 100 ohm resistors instead. Close enough.
                </h3>
                <h2>THEORETICAL BLINKY THINGS</h2>
                <Zoom>
                  <img src="A1.svg" style={{width: "65vw"}}/> 
                </Zoom>
                <br/>
                <h2>THE BLINKY THINGS IN ACTION</h2>
                <Zoom>
                  <img src="charlie_web.gif" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a1_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a1_2.jpg" style={{height: "35vh"}}/>
                </Zoom>
                
                <br/>
                <h2>THE CODE</h2>
                {/* <div style={{backgroundColor: "rgba(102, 102, 102, 0.3)", maxWidth:"70%", position:"relative", left:"15%"}}> */}
                <CodeBlock text={A1code} language="arduino" theme={obsidian}/>
                {/* </div> */}
              </div>
            }

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 2: !visible[2]}))}}>
              <h3 className="menu-text">Assignment 2: Fade!</h3>
            </div>
            {visible && visible[2] &&
              <div style={{textAlign: "left"}}>
                <h2>REASONING</h2>
                <h3>
                  For this project, I have two RGB LEDs that light up and fade over time. The color of the LEDs
                  depend on which button is pressed; blue button makes them light up blue, etc. The time it takes for
                  them to fade depends on the potentiometer. If it's twisted all the way to the left, the lights will light up
                  and fade in moments. The more it's twisted to the right, the longer it takes. Adjusting the potentiometer
                  while the LEDs are fading will either speed them up or slow them down, depending on direction. 
                  If the LEDs are currently on, the arduino will ignore all button presses. Once they're done fading,
                  the buttons will once again be able to restart the LEDs. <br/><br/>

                  All non-ground LED pins are connected to 220 OHM resistors. Red and green have a voltage drop of 1.8v (according
                  to the LED slides), 5v (from usb power) - 1.8v / 0.02A (desired current) = 160 ohms. Blue has a voltage drop of
                  3.3v, 5v - 3.3v / 0.02A = 85 ohms. Due to laziness I stuck 220 ohm resistors on all of them. There's no danger
                  going over optimal resistance, it just makes the LEDs a bit dimmer. 220 ohm isn't a big enough difference 
                  to make the LEDs TOO dim for my purposes. <br/><br/>

                  The three tactile switches and the potentiometer have 10k resistors connected. According to professor Nadya on
                  discord, in order for buttons (and the potentiometer I would assume) to work, a nominal amount of current is needed
                  and the lower the current is, the less power used. I did further googling to find out that resistors in these situations
                  are called pull-up resistors, and 10k ohms is the general rule of thumb to project against onboard noise and reduce power 
                  wasted. <br/><br/>

                  Also I hadn't realized digitalWrite() is also a requirement until the very end, so as a bonus the built-in LED
                  now turns on whenever an LED is on. Once the built-in LED turns off you're free to click buttons again.
                </h3>

                <h2>THEORETICAL FADE-Y THINGS</h2>
                <Zoom>
                  <img src="a2.svg" style={{width: "65vw"}}/> 
                </Zoom>

                <h2>FADE-Y LIGHTS IN ACTION</h2>
                <Zoom>
                  <img src="hw2.gif" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a2_1.jpg" style={{height: "35vh"}}/>
                </Zoom>
                <Zoom>
                  <img src="a2_2.jpg" style={{height: "35vh"}}/>
                </Zoom>

                <h2>THE CODE</h2>
                <CodeBlock text={A2code} language="arduino" theme={obsidian}/>
              </div>
            }

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 3: !visible[3]}))}}>
              <h3 className="menu-text">Assignment 3: Input Output!</h3>
            </div>
            {visible && visible[3] && <h2>Assignment 3 is coming in the forseeable future</h2>}

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 4: !visible[4]}))}}>
              <h3 className="menu-text">Assignment 4: Libraries!</h3>
            </div>
            {visible && visible[4] && <h2>It'll be a while before assignment 4 arrives</h2>}

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 5: !visible[5]}))}}>
              <h3 className="menu-text">Assignment 5: High(er) Voltage and transistors!</h3>
            </div>
            {visible && visible[5] && <h2>taco</h2>}

            <div className="row" style={{cursor:"pointer"}} onClick={()=>{setVisible(visible => ({...visible, 6: !visible[6]}))}}>
              <h3 className="menu-text">Assignment 6: Talking to the Web!</h3>
            </div>
            {visible && visible[6] && <h2>taco</h2>}

          </div>
        </div>

    </div>
  );
}

export default App;
